@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800);
@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css);

* {
	box-sizing: border-box;
}

#authbody {
	background: #f6f5f7;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Montserrat', sans-serif;
	height: 100vh;
	margin: -20px 0 50px;
}

h1.authbody {
	font-weight: bold;
	margin: 0;
}

h2.authbody {
	text-align: center;
}

p.authbody {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

span.authbody {
	font-size: 12px;
}

a.authbody {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

button {
	border-radius: 20px;
	border: 1px solid #2bceff;
	background-color: #2bbcff;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}

button:active {
	transform: scale(0.95);
}

button:focus {
	outline: none;
}

.ghost {
	border-color: greenyellow;
	background: lightseagreen;
}

.error {
	font-size: 120%;
}

#authbody form {
	background-color: rgb(255, 255, 255);
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

input.authbody {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.container.authbody {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25),
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 968px;
	max-width: 100%;
	min-height: 680px;
}

.form-container.authbody {
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.sign-in-container {
	left: 0;
	width: 50%;
	z-index: 2;
}

.container.right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.container.right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	-webkit-animation: show 0.6s;
	        animation: show 0.6s;
}

@-webkit-keyframes show {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}

	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

@keyframes show {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}

	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

.overlay-container {
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.container.right-panel-active .overlay-container{
	transform: translateX(-100%);
}

.overlay {
	background: #592bff;
	background: linear-gradient(to right, #592bff, #592bff);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
  	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  	transform: translateX(50%);
}

.overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.overlay-left {
	transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
	transform: translateX(0);
}

.overlay-right {
	right: 0;
	transform: translateX(0);
}

.container.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.social-container {
	margin: 20px 0;
}

.social-container a {
	border: 1px solid #DDDDDD;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	height: 40px;
	width: 40px;
}

footer {
    background-color: #222;
    color: #fff;
    font-size: 14px;
    bottom: 0;
    position: fixed;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 999;
}

footer p.authbody {
    margin: 10px 0;
}

footer i.authbody {
    color: rebeccapurple;
}

footer a.authbody {
    color: #3c97bf;
    text-decoration: none;
}
#chatBody{
    min-width: 600px;
}
#chatBody main {
    position: absolute;
    background-image: linear-gradient(to bottom right, red, yellow, red);
    height: 100%;
    width: 100%;
    margin: auto;
    overflow: scroll;
}
#chat-box {
    top: 50%;
    transform: translate(0, 9%);
    width: 75%;
    height: 75%;
}

#logo {
    max-height: 50px;
    max-width: 50px;
}

#chatList {
    height: 100%;
    background-color: #d4e4ec;
    overflow: scroll;
    border-right: 2px dashed black;
    overflow: scroll;
}
#chatList .row h3 {
    font-size: 25px;
}

#currentChat {
    height: 100%;
    background-color: #d4e4ec;
}
#messageSpace {
    height: 91%;
    padding-top: 5px;
    overflow: scroll;
}
#messageSpace .col-6 {
    height: 100%;
}
#messageForm {
    width: 100%;
    height: 100%;
}
#formHolder {
    height: 9%;
    width: 100%;
    position: absolute;
    bottom: 0;
}

#memo {
    height: 100%;
    max-width: 90%;
    width: 90%;
    min-width: 75%;

}
#msgBtn {
    height: 100%;
    max-width: 25%;
    width: 10%;
}

.conversation {
    height: 70px;
    padding-bottom: 0;
}
.conversation:hover {
    text-decoration: none;
    background-color: #b4e6ee;
}
.conversation b,
.conversation a,
.conversation p,
.conversation div {
    pointer-events: none;
}

.message-info-holder {
    width: 65%;
    height: 100%;
}
.message-info {
    width: 100%;
    font-size: 1vw;
    padding: 20px 5px 0 10px;
    color: black;
}
.message-info .col-4 {
    text-align: center;
}

.toon {
    max-width: 60px;
    max-height: 60px;
    min-width: 60px;
    min-height: 60px;
    grid-row-start: 2;
    margin-left: 5px;
}
.msg-toon {
    max-width:60px;
    max-height: 60px;
    min-width: 20px;
    min-height: 20px;
}

.status-icon {
    display: inline;
    height: 10px;
    width: 10px;
    grid-column-start: 7;
    grid-row-start: 7;
}

.toon-holder {
    display: grid;
    width: 30%;
    max-height: 85px;
    grid-template-columns: repeat(10, 8.5px);
    grid-template-rows: repeat(10, 8.5px);
}

.msg-holder {
    max-width: 70%;
}

.outgoing.msg-holder{
    margin-right: 10px;
}
.incoming.msg-holder{
    margin-left: 0;
}

.text-holder {
    width: auto;
    padding: 10px 10px 0 10px;
    background-color: white;
}
.msg-toon-holder {
    width: 15%;
}
.outgoing .msg-toon-holder img {
    float: right;
}
.outgoing .message-time {
    display: block;
    width: 100%;
    text-align: right;
}

@media (max-width: 1000px) {
    .text-holder {
        width: 85%;
    }
    .msg-toon-holder {
        width: 15%;
    }
}

@media (min-width: 1220px) {
    .text-holder {
        width: 85%;
    }
    .msg-toon-holder {
        width: 10%;
    }
    .message-info-holder {
        width: 70%;
    }
    .toon-holder {
        width: 30%;
    }
    .message-info {
        padding: 20px 5px 0 0;

    }
}

#profile body {
    min-width: 600px;
}

#profile main {
    position: absolute;
    background-image: linear-gradient(to bottom right, red, yellow, red);
    height: 100%;
    width: 100%;
    overflow: scroll;
}
#profile #chat-box {
    top: 50%;
    transform: translate(0, 9%);
    width: 75%;
    height: 75%;
}

#profile #logo {
    max-height: 50px;
    max-width: 50px;
}
#profile .imgHolder {
    max-height: 250px;
    width: auto;
}
#profile .imgHolder img {
    max-height: 250px;
    width: auto;
}
#profile .tab_btn {
    width: 200px;
}
#profile .nav-item {
    padding: 2px 5px 2px 5px;
}
#profile #cover {
    background-color: blue;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
#profile .info-holder {
    overflow: scroll;
}
button {
    background: rgba(0, 0, 0, 0);
    border: 0;
}
#profileImg {
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}
#profileImg button {
    grid-row: 7/8;
    grid-column: 5/6;
    color: black;
}
#profileImg button:hover {
    color: red;
}
